"use client";

import { motion } from 'framer-motion';
import Image from 'next/image';
import Link from 'next/link';
import ProductCard from '@/components/products/ProductCard';
import { ArrowRightIcon, ShieldCheckIcon, BeakerIcon, GlobeAltIcon } from '@heroicons/react/24/outline';

// Sample Featured Products from legacy data
const featuredProducts = [
    {
        "id": "gen-surgical-drape-pack",
        "name": "General Surgical Drape Pack",
        "category": "Surgery Packs",
        "description": "Standard sterile pack containing essential drapes for general surgical intervention.",
        "image": "https://images.unsplash.com/photo-1579684385127-1ef15d508118?auto=format&fit=crop&q=80&w=800",
        "moq": "200 Packs"
    },
    {
        "id": "ortho-drape-pack",
        "name": "Ortho Drape Pack",
        "category": "Orthopedic",
        "description": "Primary orthopedic pack including gowns and specialized limb drapes for major bone surgery.",
        "image": "https://images.unsplash.com/photo-1631815541542-e8fc9529598e?auto=format&fit=crop&q=80&w=800",
        "moq": "100 Packs"
    },
    {
        "id": "gown-sms-reinforced",
        "name": "Reinforced Surgeon Gown",
        "category": "Apparel",
        "description": "Top-tier reinforced gown for high-risk, high-fluid surgical interventions.",
        "image": "https://images.unsplash.com/photo-1631815541542-e8fc9529598e?auto=format&fit=crop&q=80&w=800",
        "moq": "200 Units"
    }
];

export default function HomePage() {
    return (
        <div className="bg-white">
            {/* Hero Section */}
            <section className="relative min-h-[90vh] flex items-center pt-20 overflow-hidden bg-slate-950">
                <div className="absolute inset-0 opacity-20">
                    <div className="grid grid-cols-6 h-full border-white/5 border-l">
                        {[...Array(6)].map((_, i) => (
                            <div key={i} className="border-r border-white/5 h-full"></div>
                        ))}
                    </div>
                </div>

                <div className="container mx-auto px-8 relative z-10">
                    <div className="max-w-4xl">
                        <motion.div
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ duration: 0.8 }}
                        >
                            <h2 className="text-medical-400 font-black uppercase tracking-[0.2em] text-sm mb-6">
                                Next-Gen Medical Procurement
                            </h2>
                            <h1 className="text-6xl md:text-8xl font-black text-white leading-[0.85] tracking-tighter uppercase mb-8">
                                Clinical <br />
                                <span className="text-gradient">Precision</span> <br />
                                at Scale.
                            </h1>
                            <p className="text-xl text-slate-400 max-w-xl mb-12 leading-relaxed">
                                Global leader in high-barrier surgical disposables and sterile medical textiles. Engineered for the world's most demanding operating environments.
                            </p>

                            <div className="flex flex-col sm:flex-row gap-6">
                                <Link
                                    href="/products"
                                    className="bg-medical-600 hover:bg-medical-700 text-white px-10 py-5 rounded-[2rem] text-sm font-black uppercase tracking-widest transition-all hover:scale-105 active:scale-95 flex items-center justify-center gap-2 group shadow-2xl shadow-medical-700/20"
                                >
                                    Explore Catalog
                                    <ArrowRightIcon className="w-4 h-4 transition-transform group-hover:translate-x-1" />
                                </Link>
                                <Link
                                    href="/customization"
                                    className="bg-white/5 backdrop-blur-xl border border-white/10 text-white px-10 py-5 rounded-[2rem] text-sm font-black uppercase tracking-widest transition-all hover:bg-white/10 flex items-center justify-center"
                                >
                                    Custom Packs
                                </Link>
                            </div>
                        </motion.div>
                    </div>
                </div>

                {/* Abstract 3D shape or decoration can go here */}
                <div className="absolute right-0 top-1/2 -translate-y-1/2 w-[40vw] h-[60vh] bg-medical-500/10 blur-[120px] rounded-full pointer-events-none"></div>
            </section>

            {/* Trust Badges */}
            <section className="py-24 border-b border-slate-100">
                <div className="container mx-auto px-8">
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-12">
                        {[
                            { icon: ShieldCheckIcon, label: "Certified Isolation", desc: "ISO 13485 & WHO-GMP compliant manufacturing protocols." },
                            { icon: BeakerIcon, label: "Advanced Textiles", desc: "Proprietary SMS and Spunbond blends for superior barriers." },
                            { icon: GlobeAltIcon, label: "Global Logistics", desc: "Trusted by hospital networks across 50+ countries." }
                        ].map((feature, i) => (
                            <motion.div
                                key={i}
                                initial={{ opacity: 0, y: 20 }}
                                whileInView={{ opacity: 1, y: 0 }}
                                viewport={{ once: true }}
                                transition={{ delay: i * 0.1 }}
                                className="flex gap-6"
                            >
                                <div className="flex-shrink-0 w-12 h-12 bg-medical-50 rounded-2xl flex items-center justify-center text-medical-600">
                                    <feature.icon className="w-6 h-6" />
                                </div>
                                <div>
                                    <h3 className="font-black text-slate-900 uppercase tracking-tight mb-2">{feature.label}</h3>
                                    <p className="text-sm text-slate-500 leading-relaxed">{feature.desc}</p>
                                </div>
                            </motion.div>
                        ))}
                    </div>
                </div>
            </section>

            {/* Featured Products */}
            <section className="py-32 bg-slate-50">
                <div className="container mx-auto px-8">
                    <div className="flex flex-col md:flex-row md:items-end justify-between mb-16 gap-8">
                        <div className="max-w-2xl">
                            <h2 className="text-4xl font-black text-slate-900 tracking-tighter uppercase leading-none mb-6">
                                Featured <span className="text-medical-600">Solutions</span>
                            </h2>
                            <p className="text-slate-500">
                                Our most in-demand clinical sterile packs and protective apparel, precision-engineered for maximum isolation performance.
                            </p>
                        </div>
                        <Link
                            href="/products"
                            className="group text-sm font-black uppercase tracking-widest text-slate-900 flex items-center gap-2 hover:text-medical-700 transition-colors"
                        >
                            View Full Catalog
                            <ArrowRightIcon className="w-4 h-4 transition-transform group-hover:translate-x-1" />
                        </Link>
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                        {featuredProducts.map((product, i) => (
                            <ProductCard key={product.id} product={product} />
                        ))}
                    </div>
                </div>
            </section>

            {/* Conversion Banner */}
            <section className="py-32">
                <div className="container mx-auto px-8">
                    <div className="bg-medical-900 rounded-[3rem] p-12 md:p-24 relative overflow-hidden group">
                        <div className="absolute top-0 right-0 w-1/2 h-full bg-medical-700/20 -skew-x-12 translate-x-1/4"></div>

                        <div className="relative z-10 max-w-3xl">
                            <h2 className="text-4xl md:text-6xl font-black text-white tracking-tighter uppercase mb-8 leading-none">
                                Looking for <br />
                                <span className="text-medical-400">Bulk Enquiry?</span>
                            </h2>
                            <p className="text-xl text-white/60 mb-12">
                                We specialize in private labeling and high-volume contract manufacturing for hospital chains and distributors.
                            </p>
                            <Link
                                href="/contact"
                                className="inline-flex bg-white hover:bg-slate-100 text-medical-900 px-12 py-5 rounded-2xl text-sm font-black uppercase tracking-widest transition-all hover:scale-105 active:scale-95 shadow-xl shadow-black/20"
                            >
                                Request Quote
                            </Link>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}
